import { Component, OnInit } from '@angular/core';

import { UserService } from '../_services/user.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.less']
})
export class UsersComponent implements OnInit {
  users = [];
  user = {
    id: 0,
    username: '',
    firstName: '',
    lastName: '',
    role: '',
    password: ''
  };

  error = '';
  errortop = '';

  constructor(private userService: UserService) { }

  ngOnInit(): void {
    this.loadUsers();
  }

  loadUsers() {
    this.userService.getAll().subscribe((res: any) => {
      console.log(res)
      this.users = res;
    })
  }

  updateUser() {
    if(!this.validate()) {
      return;
    }
    this.userService.update(this.user).subscribe((res: any) => {
      if(res.error) {
        this.error = res.error;
      }

      this.closeUpdateUser();
      this.loadUsers();
    })
  }

  validate() {
    this.error = '';
    if(this.user.username == '' || this.user.firstName == '' || this.user.lastName == '' || this.user.role == '' || this.user.password == '') {
      this.error = 'Please input all user information!';
      return false;
    }

    return true;
  }

  selectToEdit(user) {
    this.user.id = user.id;
    this.user.firstName = user.firstName;
    this.user.lastName = user.lastName;
    this.user.username = user.username;
    this.user.role = user.role;
  }

  closeUpdateUser() {
    this.user = {
      id: 0,
      username: '',
      firstName: '',
      lastName: '',
      role: '',
      password: ''
    };
  }

  selectToDelete(user) {
    this.errortop = "";
    const isDelete = confirm(`Do you want to delete ${user.username}`);
    if(!isDelete) {
      return;
    }

    this.userService.delete(user.id).subscribe((res: any) => {
      if(res.errortop) {
        this.errortop = res.error;
      }

      this.loadUsers();
    })
  }
}
