import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../_services/user.service';

@Component({
  selector: 'app-newuser',
  templateUrl: './newuser.component.html',
  styleUrls: ['./newuser.component.less']
})
export class NewuserComponent implements OnInit {

  user = {
    username: '',
    firstName: '',
    lastName: '',
    role: '',
    password: ''
  };

  error = '';

  constructor(
    private userService: UserService,
    private router: Router) { }

  ngOnInit(): void {
  }

  creatNewUser() {
    if(!this.validate()) {
      return;
    }
    this.userService.add(this.user).subscribe((res: any) => {
      if(res.error) {
        this.error = res.error;
      }

      this.router.navigate(['users']);
    })
  }

  validate() {
    this.error = '';
    if(this.user.username == '' || this.user.firstName == '' || this.user.lastName == '' || this.user.role == '' || this.user.password == '') {
      this.error = 'Please input all user information!';
      return false;
    }

    return true;
  }
}
