import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ComparisonService {

  // private REST_API_SERVER = 'http://localhost:8080/api/comparisons/'
  // private REST_API_SERVER = 'http://34.229.63.119:8080/api/comparisons/'

  constructor(
    private httpClient: HttpClient
  ) {

  }

  public getAll() {
    return this.httpClient.get(`${environment.apiUrl}/api/comparisons/all`)
  }

  public update(data) {
    return this.httpClient.put(`${environment.apiUrl}/api/comparisons/update/` + data.id, data);
  }

  public addNew() {
    return this.httpClient.post(`${environment.apiUrl}/api/comparisons/addnew`, null);
  }
}
